import React, { useContext, useState } from 'react'
import { useHistory } from 'react-router-dom'
import styled from 'styled-components'
import Dialog from '@material-ui/core/Dialog'
import { LOCAL_URL, THEME } from '../../config'
import { IconClear, BackButton, Button } from '../../components'
import { FormContainer } from './Form'
import Congratulations from '../../svg/lil_coffee_pass_congratulations.svg'

const Container = styled.div`
  width: 100%;
  padding: 82px 24px;

  text-align: center;
  line-height: 24px;
`;

const Frame = styled.div`
  padding-top: 2em;
`;
const Result = styled.div`
  width: 280px;
  height: 422px;
  padding: 2em 1.25em 2.25em;

  border-radius: 24px;
  background-color: #fff;
  text-align: center;

  position: relative;

  & button:first-child {
    position: absolute;
    top: 0;
    right: 0;
    transform: translateY(-2em);
    width: 2em;
    background: transparent;
    border: none;
  }
  & svg {
    fill: #fff;
  }
`;

export default function QuickPayPage ({location}) { 

  const history = useHistory();
  const [open, setOpen] = useState(null);
  const data = location?.state?.context;

  const onComplete = () => {
    document.documentElement.scrollTop = document.body.scrollTop = 0;
    history.push({
      pathname: LOCAL_URL.COFFEE_PASS,
      state: { 
        source: 'quick pay page',
        context: {
          activated: true
        },
      }
    })
  }
  return (
    <>
      <Container>
        <p className="ft-rr16 clr-ft-gray2-48" >{`Complete your payment within 2:00`}</p>
        <p style={{marginTop: '16px', lineHeight: '72px'}} className="ft-rb48 clr-ft-gray2-48" >{`$${9.95}`}</p>
        <p className="ft-rb16 clr-ft-gray2-48" >{`Coffee Pass - 3 Large Coffees`}</p>
        <Button style={{marginTop: '40px'}} className="large red" fullWidth={true} onClick={() => setOpen(true)}>Pay by Card</Button>
      </Container>
      <BackButton onClick={() => history.goBack()}>{' Back'}</BackButton>

      <Dialog 
        PaperProps={{ style: {
          backgroundColor: 'transparent',
          boxShadow: 'none',
        }}}
        onClose={() => setOpen(null)} 
        open={open}
      >
        <Frame>
          <Result>
            <button onClick={() => setOpen(null)} ><IconClear/></button>
            <img src={Congratulations} alt="congratulations" />
            <p style={{marginTop: '1em'}} className="ft-pb24 clr-ft-gray0-087" >{`CONGRATULATIONS!`}</p>
            <p style={{marginTop: '.5em'}} className="ft-rr12 clr-ft-gray1-32" dangerouslySetInnerHTML={{__html: `You’ve Successfully Activated your<br />Lovin’Local Coffee Pass!`}} />
            <Button style={{marginTop: '1em'}} className="large red" fullWidth={true} onClick={onComplete}>Coffee Time? Let's Go</Button>
          </Result>
        </Frame>
      </Dialog>
    </>
  )
}
