const CARDS = [
  { number: '....0505', fee: 'Surcharge 1.5%' },
]
export default function CardPage ({location}) { 
  const classes = useStyles();
  const history = useHistory();
  const [open, setOpen] = useState(null);
  const data = location?.state?.context;

  const { amountToPay } = data;
  console.log(amountToPay)

  const toPay = () => {
    // setOpen('drawer')
    history.push({
      pathname: LOCAL_URL.PAYMENT_RECEIPT,
      state: { 
        ...location?.state,
        context: { 
          code: '8E34',
          merchant: 'Auckland Cafe',
          amountToPay,
        }
      }
    });
  }
  const toPayWithNewCard = (values) => {
    console.log(values)
    toPay()
  }

  console.log(data)
  return (
    <>
      <CardContainer>

        <p className="ft-rr16 clr-ft-gray2-48">{`Complete your payment within ${'5:00'}`}</p>
        <p className="ft-rb48 clr-ft-gray0-087" style={{margin: '.6em 0 1.2em'}}>{`$${amountToPay.toFixed(2)}`}</p>
        {/* <p>{`${'30% off - Take away'}`}</p> */}

        <CardsArea>
          <CardsList>
            { CARDS.map((card, index) => (
              <CardItem key={index}>
                <div style={{display: 'flex', alignItems: 'center'}}>
                  <IconCard />
                  <span className="ft-rr16 clr-ft-gray0-087">{`${card.number}`}</span>
                  {/* <span className="tag">{`${card.fee}`}</span> */}
                </div>
                <RadioButton checked={index === 0}><IconRadioButton /></RadioButton>
              </CardItem>
            ))}
            {/* <CardItem className="empty" /> */}
          </CardsList>
        </CardsArea>

      </CardContainer>

      <Divider />

      <div style={{width: '100%', padding: '24px 24px'}} >
      { (open !== 'new card') ? 
        <AddButton onClick={() => setOpen('new card')}>
          <span style={{fontFamily: 'Roboto', fontSize: '2em', fontWeight: 100, marginRight: '.2em'}}>+</span>
          <span className="ft-rm14 clr-ft-gray0-087">Add Another Card</span>
        </AddButton>
        :
        <CardNewForm onSubmit={toPayWithNewCard}/>
      }
      </div>

      { (open === 'new card') ?  null :
        <div style={{width: '100%', padding: '14px 24px'}} >
          <Button variant="large red" fullWidth={true} onClick={toPay} >Pay Now</Button>
        </div>
      }

      <BackButton onClick={() => history.goBack()}>{' Payment Method'}</BackButton>

      {/* <Drawer classes={{ paper: classes.paper }} anchor="bottom" style={{background: 'transparent'}} open={open === 'drawer'} onClose={() => setOpen(false)} >
        <Receipt 
          onClose={() => setOpen(false)} 
          code={'8E34'} 
          notice={'Show this unique code to the merchant now'}
          merchant={'Shanghai Cafe'}
        />
      </Drawer> */}

    </>
  )
}
