import React, { useState, useRef, useEffect } from 'react';
import styled from 'styled-components';
import { THEME } from '../config';
import { IconKBArrowL, IconKBArrowR } from './Icons';

const VIEW_MODE_THRETHOLD = THEME.size.viewModeThreshold;

const CarouselContainer = styled.div`
  width: 100%;
  height: 100%;
`;
const CarouselWindow = styled.div`
  width: 100%;
  height: 100%;
  overflow: hidden;

  position: relative;

  & img {
    width: 100%;
    height: 100%;
    object-fit: cover;
    background-position: center;
  }
`;

const SlideList = styled.div`
  width: ${(props) => `${props.size * 100}%`};
  height: 100%;
  transform: ${(props) => `translateX(-${ 100 * props.offset / props.size }%)`};
  transition: all .6s;

  display: inline-flex;
`;
const Slide = styled.div`
  width: 100%;
  height: 100%;
  display: inline-block;
  position: relative;
`;
const SlideContentArea = styled.div`
  position: absolute;
  top: 50%;
  transform: translateY(-50%);

  left: 10%;
  width: 36%;
  color: #fff;
  & h1 {
    font-size: 56px;
    line-height: 64px;
  }
  & p {
    margin-top: 2em;
    width: 70%;
    font-size: 14px
  }

  @media screen and (max-width: ${VIEW_MODE_THRETHOLD}) {
    width: 100%;
    top: 0;
    transform: none;
    left: 0;
    display: flex;
    flex-direction: column;
    justify-content: space-around;
    align-items: center;
    & h1 {
      white-space: nowrap;
      font-size: 24px;
    }
    & p {
      width: 90%;
      margin: 0;
      padding: 0 1em;
      text-align: center;
    }
  }
`;
const ArrowButton = styled.button`
  position: absolute;
  top: 50%;
  transform: translateY(-50%);
  background: transparent;
  border: none;
  border-radius: .5em;
  width: 1em;
  height: 1em;
  font-size: 36px;
  color: rgba(255,255,255,0.8);
  text-align: center;
  cursor: pointer;

  & svg {
    fill: rgba(255,255,255,0.6);
  }
  &.left {
    left: .2em;
  }
  &.right {
    right: .2em;
  }
  &:hover {
    background: rgba(0,0,0,0.3);
  }
  &:hover svg {
      fill: #fff;
    }

  @media screen and (max-width: ${VIEW_MODE_THRETHOLD}) {
    font-size: 20px;
  }
`;
const RadioGroup = styled.div`
  position: absolute;
  bottom: 1em;
  left: 50%;
  transform: translateX(-50%);

  @media screen and (max-width: ${VIEW_MODE_THRETHOLD}) {
    bottom: .3em;
  }
`;
const RadioButton = styled.input`
  margin: 0 .3em;
  cursor: pointer;
`;

export const Carousel = ({items, delay, noArrow}) => { 
  const [activeId, setActiveId] = useState(2);
  const timerId = useRef(null);

  const nextSlide = () => {
    setActiveId(  ( (activeId + 1) < items.length ) ? (activeId + 1) : 0 );
  }
  const prevSlide = () => {
    setActiveId(  ( (activeId - 1) > 0 ) ? (activeId - 1) : (items.length - 1) );
  }

  useEffect(() => {
    timerId.current = window.setInterval(() => {
      setActiveId((activeId) => (
        ( (activeId + 1) < items.length ) ? (activeId + 1) : 0
      ));
    }, delay);

    return (() => {
      if (!!timerId?.current) {
        window.clearInterval(timerId.current);
      }
    });
  }, []);

  return (
    <CarouselContainer>
      <CarouselWindow>

        <SlideList size={items.length} offset={activeId}>
        { items.map((item, index) => (
          <Slide key={index}>
            <img key={index} src={item.img} alt={`image #${index}`} />
            { (!item.title) ? null :
              <SlideContentArea>
                <h1>{`${item.title} ${index}`}</h1>
                <p>{item.comments}</p>
              </SlideContentArea>
            }
          </Slide>
        ))}
        </SlideList>

        <RadioGroup>
        { items.map((item, index) => (
          <RadioButton key={index} type='radio' checked={index === activeId} onClick={() => setActiveId(index)} />
        ))}
        </RadioGroup>

        { !!noArrow ? null :
          <>
          <ArrowButton className='left'  onClick={prevSlide} ><IconKBArrowL /></ArrowButton>
          <ArrowButton className='right' onClick={nextSlide} ><IconKBArrowR /></ArrowButton>
          </>
        }
      
      </CarouselWindow>
    </CarouselContainer>
  )
}
