import React, { useState } from 'react';
import styled from 'styled-components';
import { IconAddCircle, IconRemoveCircle } from './Icons';
import { THEME } from '../config';

export const QuantityContainer = styled.div`
  width: 100%;
  text-align: center;
`;
export const Counter = styled.div`
  width: 100%;
  display: flex;
  justify-content: center;
  align-items: center;

  & svg {
    width: 2em;
    height: 2em;
    cursor: pointer;
    transform: translateY(.16em);
    fill: ${THEME.color.gray['#0-087']};
  }
  & button {
    background: transparent;
    border: none;
  }
`;

interface QuantityProps {
  onChange?: (value: number) => void;
  value: number;
  limit?: number;
}
export const Quantity: React.FC<QuantityProps> = ({value, limit, onChange}) => {
  const [quantity, setQuantity] = useState(value);
  const add = () => { 
    setQuantity( (q) => {
      if (!!limit && (q >= limit)) return q;
      // else 
      if (!!onChange) onChange(q+1);
      return (q+1);
    }); 
  }
  const sub = () => { 
    setQuantity( (q) => {
      if (q === 0) return q;
      // else 
      if (!!onChange) onChange(q-1);
      return (q-1); 
    });
  }

  return (
    <QuantityContainer>
      <p style={{lineHeight: '24px'}} className="ft-ps16 clr-ft-gray2-48">Quantity</p>
      <Counter>
        <button onClick={sub}><IconRemoveCircle /></button>
        <p style={{margin: '0 16px', lineHeight: '48px'}} className="ft-rb32 clr-ft-gray0-087">{value}</p>
        <button onClick={add}><IconAddCircle /></button>
      </Counter>
      { !limit ? null : <p>{`You still have ${limit - value} left`}</p> }
    </QuantityContainer>
  )
}
