import React, { useContext, useState } from 'react';
import { useHistory } from 'react-router-dom';
import styled from 'styled-components';
import { UserContext } from '../../context';
import { LOCAL_URL, THEME } from '../../config';
import { 
  IconKBArrowR, IconBillDiscount, IconBillVoucher,
  Button, BackButton, 
} from '../../components';

const VIEW_MODE_THRETHOLD = THEME.size.viewModeThreshold;

const BillContainer = styled.div`
  margin-top: 58px;
  width: 100%;
`;
const ListItem = styled.div`
  height: 72px;
  padding: 0 24px;
  display: flex;
  flex-wrap: nowrap;
  justify-content: space-between;
  align-items: center;

  & span.title svg {
    margin-right: 18px;
    width: 28px;
    height: 28px;
    transform: translateY(25%);
    fill: ${THEME.color.primary.default};
  }
  & span.money svg {
    width: 14px;
    height: 14px;
    transform: translate(50%, 12%);
    fill: ${THEME.color.gray['#1-32']};
  }
`;
const DiscountItem = styled(ListItem)`
  border-bottom: 1px solid ${THEME.color.gray['#6-d9']};
`;
const VoucherItem = styled(ListItem)`
  border-bottom: 1px solid ${THEME.color.gray['#6-d9']};
`;
const TotalAmount = styled(ListItem)`
  padding-left: 16px;
`;
const AmountToPay = styled(ListItem)`
  padding-left: 16px;
  height: 20px;
`;

export default function BillPage ({location}) { 
  const history = useHistory();
  const data = location?.state?.context;

  const { discount, voucher, amountToPay } = data;
  const discountAmount = !discount ? 0 : discount * amountToPay;

  const onPay = () => {
    document.documentElement.scrollTop = document.body.scrollTop = 0;
    history.push({
      pathname: LOCAL_URL.PAYMENT_CARD,
      state: { 
        source: 'payment bill page',
        context: {
          amountToPay: (amountToPay - discountAmount)
        },
      }
    })
  }

  console.log(data)

  return (
    <>
      <BillContainer>

        <DiscountItem>
          <span className="title ft-rb16 clr-ft-gray0-087"><IconBillDiscount />{`${(discount * 100)}% off discount`}</span>
          <span className="money ft-rb18 clr-ft-gray3-76">{`-$${discountAmount.toFixed(2)}`}<IconKBArrowR /></span>
        </DiscountItem>
        
        <VoucherItem>
          <span className="title ft-rb16 clr-ft-gray0-087"><IconBillVoucher />{`${voucher || 'No'} voucher`}</span>
          <span className="money ft-rb18 clr-ft-gray3-76">{`-$${'0.00'}`}<IconKBArrowR /></span>
        </VoucherItem>
        
        <TotalAmount>
          <span className="ft-rm14 clr-ft-gray2-48">Total Bill Amont</span>
          <span className="ft-rb18 clr-ft-gray3-76" >{`$${amountToPay.toFixed(2)}`}</span>
        </TotalAmount>
        
        <AmountToPay>
          <span className="ft-rb18 clr-ft-gray2-48">Amont to Pay</span>
          <span  className="ft-rb24 clr-ft-green">{`$${(amountToPay - discountAmount).toFixed(2)}`}</span>
        </AmountToPay>

      </BillContainer>

      <div style={{width: '100%', padding: '48px 24px'}} >
        <Button fullWidth={true} onClick={onPay}>Pay Now</Button>
      </div>

      <BackButton onClick={() => history.goBack()}>{' Bill'}</BackButton>
    </>
  )
}
