import React, { useState } from 'react';
import { Link } from 'react-router-dom';
import styled from 'styled-components';
import { Section, StretchColumn, Tag, IconFilter } from '../../components';
import { THEME } from '../../config';

const VIEW_MODE_THRETHOLD = THEME.size.viewModeThreshold;
const MAX_WIDTH  = THEME.size.maxWidth.body;
const SIDE_SPACE = THEME.size.sideSpace;

const SegmentSection = styled(Section)`
  margin-top: 1px;
  padding: .8em 0;
  font-size: 14px;
  @media screen and (min-width: ${VIEW_MODE_THRETHOLD}) {
    display: none;
  }
`;
const SegmentStretch = styled.div`
  margin-left: ${SIDE_SPACE};
  @media screen and (min-width: ${MAX_WIDTH}) {
    max-width: ${MAX_WIDTH};
    margin: 0 auto;
  }
`;

const HeaderArea =styled.div`
  width: 100%;
  overflow: hidden;
  display: inline-flex;
  justify-content: space-between;
  @media screen and (min-width: ${MAX_WIDTH}) {
    justify-content: start;
  }

  & svg {
    width: 30px;
  }
`;

export const PopularHeader = ({categories}) => { 
  return (
    <SegmentSection>
      <SegmentStretch>

        <HeaderArea>
          <Tag className="ft-rr14 clr-ft-gray2-48 tag gray"><IconFilter /></Tag> 
          { categories.map((category, index) => (
            <Tag key={index} className="ft-rr14 clr-ft-gray2-48 gray">{category.label}</Tag> 
          )) }
        </HeaderArea>
      
      </SegmentStretch>
    </SegmentSection>
  )
}
