import React, { useEffect, useState } from 'react'
import styled from 'styled-components'
import { THEME } from '../config'

const HEIGHT = 16;
const WIDTH  = 16;
const UNIT   = 'px';
const TOTAL_WIDTH = 26;
const CHECKED_POS = 11;

const Container = styled.div`
  width: ${TOTAL_WIDTH}${UNIT};
  min-height: ${HEIGHT}${UNIT};
  border-radius: ${HEIGHT/2}${UNIT};
  background: ${(props) => props.checked ? THEME.color.accent.green : THEME.color.gray['#5-025'] };

  position: relative;
  display: inline-flex;
`;
const Stick = styled.div`
  width: ${HEIGHT-2}${UNIT};
  height: ${HEIGHT-2}${UNIT};
  border-radius: ${(HEIGHT-2)/2}${UNIT};
  position: absolute;
  top: 1px;
  left: ${(props) => !props.checked ? '1px' : `${CHECKED_POS}${UNIT}`};
  background: #fff;
  transition: all .3s;
`;

interface SwitchProps {
  onChange?: () => void;
  value?: boolean;
}
export const Switch: React.FC<SwitchProps> = ({onChange, value}) => {
  const [checked, setChecked] = useState(!!value);
  const onClick = () => {
    setChecked((current) => (!current));
  }
  
  return (
    <Container onClick={onClick} checked={checked}>
      <Stick checked={checked}/>
    </Container>
  )
}
