import React, { useState, useEffect } from 'react';
import { useHistory } from 'react-router-dom';
import styled from 'styled-components';
import { Section, StretchColumn, Button, Ticket, TicketContent, TicketDash  } from '../../components';
import { LOCAL_URL } from '../../config';
// const VIEW_MODE_THRETHOLD = THEME.size.viewModeThreshold;

const SegmentSection = styled(Section)`
  padding: 1em 0;
  background: transparent;
`;
const SegmentStretch = styled(StretchColumn)`
`;
const DEALS = [
  { showExcluded: true, discount: 0.3, title: '30% Off - Take Away', details: ['Minimum Order = $50', 'Hours Valid: 5pm - 9pm', 'Expires: dd/mm/yy'] },
  { showExcluded: false,discount: 0.2, title: '20% Off - Dine In',   details: ['No Minimum Order' ] },
]

export const InStoreDeals = () => { 
  const history = useHistory();

  const onClick = (deal) => () => {
    document.documentElement.scrollTop = document.body.scrollTop = 0;
    history.push({
      pathname: LOCAL_URL.PAYMENT_AMOUNT,
      state: { 
        source: 'in-store deals',
        context: {
          ...deal,
          textHtml: 'Time to Pay?<br />Enter the TOTAL BILL amount<br />(before any discount)',
          voucher: null,
        },
    }})
  }
  return (
    <SegmentSection>
      <SegmentStretch>
        <p className="ft-pm18 clr-ft-gray0-087">In-store Deals</p>
        { DEALS.map((deal, index) => (
          <Ticket key={index} onClick={onClick(deal)}>
            <TicketContent {...deal} />
            <Button variant="medium white">ACTIVATE</Button>
            <TicketDash />
          </Ticket>
        )) }
      </SegmentStretch>
    </SegmentSection>
  )
}
