import React, { useContext, useState, useEffect } from 'react';
import { useHistory } from 'react-router-dom';
import styled from 'styled-components';
import { makeStyles } from '@material-ui/core/styles';
import Drawer from '@material-ui/core/Drawer';
import { UserContext } from '../../context';
import { DoubleConfirm, BackButton, FavoriteButton, Navbar } from '../../components';
import { CarouselSection } from './Carousel';
import { StoreInfo } from './Info';
import { CoffeePass } from './CoffeePass';
import { InStoreDeals } from './InStoreDeals';
import { DealsGroup } from './DealsGroup';
import { LOCAL_URL, THEME } from '../../config';
import { Order } from './Order';
import { RedeemForm } from './Redeem';

import CoffeePassIcon from '../../svg/lil_coffeepass.svg';
import HotDealIcon from '../../svg/lil_hotdeals.svg';

const useStyles = makeStyles({
  paper: {
    background: 'transparent',
  },
});

const VIEW_MODE_THRETHOLD = THEME.size.viewModeThreshold;

const SLIDE_ITEMS = [
  { img: 'https://images.unsplash.com/photo-1607419727375-6dd45089fced?ixid=MXwxMjA3fDB8MHx0b3BpYy1mZWVkfDEwfHhIeFlUTUhMZ09jfHxlbnwwfHx8&ixlib=rb-1.2.1&auto=format&fit=crop&w=500&h=260&q=60' },
  { img: 'https://images.unsplash.com/photo-1589368089219-27f691d79532?ixid=MXwxMjA3fDB8MHx0b3BpYy1mZWVkfDM1fHhIeFlUTUhMZ09jfHxlbnwwfHx8&ixlib=rb-1.2.1&auto=format&fit=crop&w=500&h=260&q=60' },
  { img: 'https://images.unsplash.com/photo-1613262973691-667269233c68?ixid=MXwxMjA3fDB8MHx0b3BpYy1mZWVkfDcwfHhIeFlUTUhMZ09jfHxlbnwwfHx8&ixlib=rb-1.2.1&auto=format&fit=crop&w=500&h=260&q=60' },
];

const COFFEE_PASS = [
  { icon: CoffeePassIcon, title: 'Coffee Pass', details: 'Available here' },
  { icon: HotDealIcon,    title: 'Hot Deals',   details: '1 x Kiwi Burger' },
]

const DEALS = [
  { price: 9.95, listPrice: 14.95, name: 'Kiwi Burger',                   group: 'Hot Deals',          cover: 'https://images.unsplash.com/photo-1606307305289-95593c01f986?ixid=MXwxMjA3fDB8MHx0b3BpYy1mZWVkfDUzNnx4alBSNGhsa0JHQXx8ZW58MHx8fA%3D%3D&ixlib=rb-1.2.1&auto=format&fit=crop&w=500&q=60', },
  { price: 8.99, listPrice: 13.99, name: 'Large Coffee + Slice of Cake',  group: 'Combination Deals',  cover: 'https://images.unsplash.com/photo-1608835149345-b4d77bc490ae?ixid=MXwxMjA3fDB8MHx0b3BpYy1mZWVkfDQzMXx4alBSNGhsa0JHQXx8ZW58MHx8fA%3D%3D&ixlib=rb-1.2.1&auto=format&fit=crop&w=500&q=60' },
]

const POST_ACTIONS = ['View My Voucher', 'Redeem Voucher Now' ];
const globalCache = { current: null };

export default function StorePage ({location}) { 
  const history = useHistory();
  const classes = useStyles();
  const [redeemPass,  setRedeemPass]  = useState(null);
  const [orderDeal, setOrderDeal] = useState(null);
  let data = location?.state?.context;
  if (!!data) globalCache.current = data;     // 如果从首页的店铺列表进入店铺详情页的时候，context 是有效的，那么缓存当前店铺信息
  else data = globalCache.current;            // 如果从某些页面，通过特殊路由，返回当前店铺的时候，context 可能是无效的，那么用之前缓存的店铺信息


  const groups = Array.from(new Set(DEALS.map(deal => deal.group)));

  const toPay = () => {
    document.documentElement.scrollTop = document.body.scrollTop = 0;
    history.push({
      pathname: LOCAL_URL.PAYMENT_AMOUNT,
      state: { 
        source: 'in-store payment',
        context: {
          textHtml: 'Time to Pay?<br />Enter the TOTAL BILL amount',
          showExcluded: false,
          voucher: null,
          discount: 0,
        },
      }
    })
  }

  const toRedeem = (pass) => { setRedeemPass(pass); }
  const onConfirmPass = () => {
    document.documentElement.scrollTop = document.body.scrollTop = 0;
    setRedeemPass(null);
    history.push({
      pathname: LOCAL_URL.REDEEM,
      state: { 
        source: 'store page',
        context: { },
      }
    })
  }

  const toOrder = (deal) => { setOrderDeal(deal); }
  const onConfirmOrder = (amountToPay: number) => {
    document.documentElement.scrollTop = document.body.scrollTop = 0;
    setOrderDeal(null);
    history.push({
      pathname: LOCAL_URL.PAYMENT_CARD,
      state: { 
        source: 'store deal order',
        context: {
          amountToPay
        },
        postActions: POST_ACTIONS,
      }
    })
  }
  useEffect(() => {
    const { postAction } = location?.state;
    if (postAction === POST_ACTIONS[1]) {
      toRedeem('x');
    }
  }, [])

  // console.log(orderDeal)
  return (
    <>
      <CarouselSection delay={3000} items={SLIDE_ITEMS} noArrow={true} />
      <StoreInfo info={data} toPay={toPay} />

      { COFFEE_PASS.map((cp) => (
        <CoffeePass key={cp.title} toRedeem={() => toRedeem('x')} {...cp} />
      )) }
      
      <InStoreDeals />

      { groups.map((groupName) => (
        <DealsGroup key={groupName} title={groupName} deals={DEALS.filter(deal => deal.group === groupName)} onOrder={toOrder} />
      )) }

      <BackButton color="#fff" onClick={() => history.goBack()}>{' Back'}</BackButton>
      <FavoriteButton color="#fff" onClick={() => console.log('favorite click')} />
      <Navbar />

      <Drawer classes={{ paper: classes.paper }} anchor="bottom" style={{background: 'transparent'}} open={!!orderDeal} onClose={() => setOrderDeal(null)} >
        <Order 
          onClose={() => setOrderDeal(null)} 
          onConfirm={onConfirmOrder}
          deal={orderDeal}
          notice={'Expires on dd/mm/yyyy'}
          merchant={'Shanghai Cafe'}
        />
      </Drawer>

      <Drawer classes={{ paper: classes.paper }} anchor="bottom" style={{background: 'transparent'}} open={!!redeemPass} onClose={() => setRedeemPass(null)} >
        <DoubleConfirm onClose={() => setRedeemPass(null)} onConfirm={onConfirmPass} >
          <RedeemForm onClose={() => setRedeemPass(null)} />
        </DoubleConfirm>
      </Drawer>
    </>
  )
}
