import React, { useState } from 'react';
import { useHistory } from 'react-router-dom';
import styled from 'styled-components';
import { Section, StretchColumn } from '../../components';
import { THEME, LOCAL_URL } from '../../config';

const MAX_WIDTH  = THEME.size.maxWidth.body;
const SIDE_SPACE = THEME.size.sideSpace;

const SegmentSection = styled(Section)`
  padding: 1em 0;
`;
export const SegmentStretch = styled.div`
  margin-left: ${SIDE_SPACE};
  @media screen and (min-width: ${MAX_WIDTH}) {
    max-width: ${MAX_WIDTH};
    margin: 0 auto;
    padding: 0 0 1em 0;
    border-bottom: 1px solid ${THEME.color.gray['#7-f5']};
  }
`;

const ViewWindow = styled.div`
  width: 100%;
  @media screen and (max-width: ${MAX_WIDTH}) {
    overflow-x: scroll;
    white-space: nowrap;

    scrollbar-width: none;
    &:-webkit-scrollbar {
      display: none;
    }
  }
`;
const ContentList = styled.div`
  display: inline-flex;
  @media screen and (min-width: ${MAX_WIDTH}) {
    width: 100%;
    justify-content: space-between;
  }
`;
const ListItem = styled.div`
  width: 6.3em;
  display: flex;
  flex-direction: column;
  justify-content: space-between;
  align-items: center;

  cursor: pointer;
`;
const ItemIcon = styled.div`
  width: 48px;
  height: 48px;
  & img {
    border-radius: 50%;
  }
`;

export const OfferingCategories = ({items}) => { 
  const history = useHistory();
  const gotoPage = () => {
    document.documentElement.scrollTop = document.body.scrollTop = 0;
    history.push({
      pathname: LOCAL_URL.COFFEE_PASS
    })
  }
  return (
    <SegmentSection>
      <SegmentStretch>

        <ViewWindow>
          <ContentList>
          { items.map((item, index) => (
            <ListItem key={index} onClick={() => gotoPage()}>
              <ItemIcon><img src={item.icon} /></ItemIcon>
              <p className="ft-pm12 clr-ft-gray0-087">{item.name}</p>
            </ListItem>
          )) }
          </ContentList>
        </ViewWindow>
      
      </SegmentStretch>
    </SegmentSection>
  )
}
