import React, { useContext, useState } from 'react';
import { UserContext } from '../../context';
import EmailForm from './Email';
import PasswordForm from './Password';
import UserNameForm from './UserName';
import PhoneNumberForm from './PhoneNumber';
import DigitCodeForm from './DigitCode';
import FinalResult from './Result';

const StepForm = ({onSubmit, initValues, step, ...props}) => {
  return (
    ( 0 === step ) ? <EmailForm       onSubmit={onSubmit} initValues={initValues} {...props} /> :
    ( 1 === step ) ? <PasswordForm    onSubmit={onSubmit} initValues={initValues} {...props} /> :
    ( 2 === step ) ? <UserNameForm    onSubmit={onSubmit} initValues={initValues} {...props} /> :
    ( 3 === step ) ? <PhoneNumberForm onSubmit={onSubmit} initValues={initValues} {...props} /> :
    ( 4 === step ) ? <DigitCodeForm   onSubmit={onSubmit} initValues={initValues} {...props} /> :
    ( 5 === step ) ? <FinalResult     onSubmit={onSubmit} initValues={initValues} {...props} /> :
    null
  )
}

export default function CredentialForm ({ onFinish }) {
  const { login } = useContext(UserContext);
  const [data, setData] = useState<any>({});
  const [step, setStep] = useState<number>(0);

  const onSubmit = (values) => {
    console.log(`step: ${step} `, values);
    if (
      (data.email       === 'user@live.com') &&
      (values.password  === '123456')
    ) {
      login({ credential: 'user@live.com' });
      if (!!onFinish) onFinish();
    }
    else {
      setData({...data, ...values});
      setStep(step+1);
    }
  }

  return (
    <StepForm step={step} initValues={data} onSubmit={onSubmit} />
  )
}
