import React from 'react';
// import { useHistory } from 'react-router-dom';
import { useForm } from 'react-hook-form';
import { yupResolver } from '@hookform/resolvers/yup';
import * as yup from 'yup';
import { SubmitButton, Input } from '../../components';
import { Form } from './Form';

const FORM_TITLE = `<h1>It’s Password Time</h1>`;
const FORM_COMMENTS = '<p>Simply enter your desired password</p><p>(minimum of 6 characters/numbers)</p>';

const schema = yup.object().shape({
  password: yup
            .string()
            .test('len', 'Password must have minimum of 6 haracters', (s) => (s.length >= 6) )
            .required('Password is required'),
});

export default function PasswordForm ({onSubmit, initValues}) {
  const { register, handleSubmit, errors,  formState: { isDirty } } = useForm({
    defaultValues: { ...initValues },
    mode: 'onBlur',
    resolver: yupResolver(schema),
  });
  // const history = useHistory();

  return (
    <Form titleHtml={FORM_TITLE} commentsHtml={FORM_COMMENTS} onSubmit={handleSubmit(onSubmit)}>
      <Input
        ref={register}
        error={!!errors.password}
        helperText={errors?.password?.message}
        type='password'
        name='password'
        placeholder='Enter Your Password Here'
      />
      <SubmitButton disabled={!isDirty} >Save My Password</SubmitButton>
    </Form>
  )
}
