import React, { useState } from 'react';
import styled from 'styled-components';
import { DrawerBox, DrawerCloseButtonArea, DrawerContentContainer } from './common.styled';
import { DragConfirmButton } from './Button';
import { IconClear } from './Icons';
import { THEME } from '../config';
import AMOUNT_FORM_IMAGE from '../svg/double_confirm.svg';

interface DoubleConfirmProps {
  children?: any;
  onClose: () => void;
  onConfirm: () => void;
}
export const DoubleConfirm: React.FC<DoubleConfirmProps> = ({ children, onClose, onConfirm }) => {
  const [preConfirm, setPreConfirm] = useState(false);

  const subItems =  (children instanceof Array) ? children :
                    ('object' === typeof children) ? [children] :
                    []
                    ;
  
  if (!preConfirm) return (
    <>
    { 
      subItems.map((child, index) => {
        const tab = child?.props?.tab;
        const newChild = React.cloneElement(child, { 
          ...child?.props,
          onConfirm: () => setPreConfirm(true)
        });
        return(newChild);
    })
    }
    </>
  )

  else return (
    <DrawerBox>
      <DrawerContentContainer>

        <img style={{marginTop: '32px'}} src={AMOUNT_FORM_IMAGE} alt="form image" /> 

        <p style={{marginTop: '32px', lineHeight: '24px'}} className="ft-rb16 clr-ft-primary" dangerouslySetInnerHTML={{ __html: 'Confirm you’ve ordered your Hot Deal and are<br />ready to provide your Redemption Code.' }} />

        <div style={{marginTop: '16px'}}><DragConfirmButton onConfirm={onConfirm} /></div>

        <DrawerCloseButtonArea onClick={onClose} ><IconClear /></DrawerCloseButtonArea>

      </DrawerContentContainer>
    </DrawerBox>
  )
}
