import React, { useState, useRef } from 'react';
// import { useHistory } from 'react-router-dom';
import styled from 'styled-components';
import TextField, { TextFieldProps } from '@material-ui/core/TextField';
import { FormContainer, FormHeader, FormComments } from './Form';

const FORM_TITLE    = `<h1>Enter the 4 Digit</h1><h1>Code Sent to</h1>`;
const DIGIT_WARNING = `<p>Resend Code</p><p>send to a different number</p>`;
const DIGIT_COMMENTS= `<p>Standard message and data rates may apply</p>`;

const DigitGroup = styled.div`
  margin: 18px 0;
  width: 100%;
  display: inline-flex;
  justify-content: space-between;
`;
const DigitWarning = styled.div`
  font-size: 14px;
  font-weight: 600;
  text-transform: uppercase;
  text-align: center;
  line-height: 24px;
  color: red;
`;
const DigitComments = styled.div`
  font-size: 11px;
  text-align: center;
  line-height: 24px;
  color: #bdbdbd;
`;
const DigitInput = React.forwardRef<unknown, TextFieldProps>((props, ref) => {
  return (
  <TextField
    type="text"
    variant='outlined' 
    margin="normal" 
    // fullWidth
    label={null}
    inputProps={{style: { textAlign: 'center', margin: '0 6px' }}} 
    InputLabelProps={{shrink: false}}
    inputRef={ref}
    {...props}
  />
  )
})
export default function DigitCodeForm ({onSubmit, initValues}) {
  const [code, setCode] = useState([null, null, null, null]);
  const ref1 = useRef(null);
  const ref2 = useRef(null);
  const ref3 = useRef(null);
  const ref4 = useRef(null);
  const inputRefs = [ref1, ref2, ref3, ref4];

  const onChange = (index) => (e) => {
    const c = e.target.value;
    if (!!c) {
      code[index] = c;
      setCode([...code]);
      if (index < 3) inputRefs[index+1].current.focus();
      else onSubmit({code: code.join('')});
    }
  }
  const onFocus = (index) => (e) => {
    // console.log(index, e);
    code[index] = '';
    setCode([...code]);
  }
  const commentsHtml = `<p>${initValues.phoneNumber}</p>`;
  return (
    <FormContainer>
      <FormHeader dangerouslySetInnerHTML={{__html: FORM_TITLE}} />
      <FormComments dangerouslySetInnerHTML={{__html: commentsHtml}} />
      <DigitGroup>
      { [1,2,3,4].map((n, index) => (
        <DigitInput key={index} autoFocus={n === 1} value={code[index]} onFocus={onFocus(index)} onChange={onChange(index)} ref={inputRefs[index]} />
      )) } 
      </DigitGroup>
      <DigitWarning dangerouslySetInnerHTML={{__html: DIGIT_WARNING}} />
      <DigitComments dangerouslySetInnerHTML={{__html: DIGIT_COMMENTS}} />
    </FormContainer>
  )
}
