import { IconStar, IconLocator } from './Icons'
import { THEME } from '../config'

export const VendorRatingLocation = ({rating, distance}) => {
  const stars = new Array(parseInt(rating, 10));
  stars.fill('⭐', 0);
  return (
    <p>
      <span>{`${rating.toFixed(1)} ${stars.join('')}`}</span>
      <span style={{marginLeft: '.4rem'}}>
        <IconLocator />
        {`${distance}`}
      </span>
    </p>
  )
}

export const VendorRatingLocation2 = ({name, rating, distance}) => (
  <div>
    <p className="ft-pm18 clr-ft-gray0-087">{name}</p>
    <p>
      <span className="ft-rm12 clr-ft-gray2-48" style={{marginRight: '.2rem'}}>{rating.toFixed(1)}</span>
      { [0,1,2,3,4].map((i) => (
        (i > rating) ? null : <span key={i} className="yellow"><IconStar /></span>
      )) }
      <span className="blue" style={{marginLeft: '.4rem'}}><IconLocator /></span>
      <span className="ft-rr12 clr-ft-gray4-97">{distance}</span>
    </p>
  </div>
)
