import React, { useState } from 'react';
import { useHistory } from 'react-router-dom';
import styled from 'styled-components';
import { VendorRatingLocation } from './others';
import { LOCAL_URL, THEME, PROVIDER_ITEM_WIDTH } from '../config';

const VIEW_MODE_THRETHOLD = THEME.size.viewModeThreshold;

const ProviderContainer = styled.div`
  position: relative;
  width: ${(props) => (props.size === 'big') ? `${PROVIDER_ITEM_WIDTH.MOBILE}px` : '100%'};
  margin: 1em 0;
  line-height: 16px;

  display: flex;
  flex-direction: column;
  justify-content: space-between;
  align-items: center;

  @media screen and (min-width: ${VIEW_MODE_THRETHOLD}) {
    width: ${(props) => (props.size === 'big') ? `${PROVIDER_ITEM_WIDTH.DESKTOP}px` : '30%'};
  }
`;
const ProviderTag = styled.div`
  position: absolute;
  background: #53A35D;
  color: #fff;
  top: 6px;
  right: 6px;
  height: 24px;
  padding: 3px 12px 0 8px;
  border-radius: 12px;
  font-size: 12px;
  @media screen and (min-width: ${VIEW_MODE_THRETHOLD}) {
    top: ${(props) => (props.size !== 'big') ? '6px' : '12px'};
    right: ${(props) => (props.size !== 'big') ? '6px' : '12px'};
    height: ${(props) => (props.size !== 'big') ? '24px' : '48px;'};
    padding: ${(props) => (props.size !== 'big') ? '3px 12px 0 8px' : '14px 14px 0 8px'};
    border-radius: ${(props) => (props.size !== 'big') ? '12px' : '24px'};
    font-size: ${(props) => (props.size !== 'big') ? '12px' : '24px'};
  }

`;
const ProviderCover = styled.div`
  width: 100%;
  height: 10em;

  & img {
    width: 100%;
    height: 100%;
    object-fit: cover;
    background-position: center;
  }
`;
const ProviderFooter = styled.div`
  width: 100%;
  margin-top: .6em;
  display: inline-flex;
  justify-content: space-between;

  cursor: pointer;
`;
const ProviderBrief = styled.div`
  & h4 {
    font-weight: normal;
    font-size: 14px;
  }
  & p {
    font-size: 12px;
  }
  & svg {
    width: 16px;
    fill: ${THEME.color.accent.blue};
    transform: translateY(4px);
  }
  @media screen and (min-width: ${VIEW_MODE_THRETHOLD}) {
    & h4 {
      font-size: ${(props) => (props.size !== 'big') ? '14px' : '24px'};
      line-height: ${(props) => (props.size !== 'big') ? '16px' : '32px'};
    }
    & p {
      font-size: ${(props) => (props.size !== 'big') ? '12px' : '16px'};
      line-height: ${(props) => (props.size !== 'big') ? '16px' : '32px'};
    }
  }
`;
const ProviderIcon = styled.div`
  width: 32px;
  height: 32px;
  & img {
    background: #FAEBC8;
    border-radius: 50%;
  }
  @media screen and (min-width: ${VIEW_MODE_THRETHOLD}) {
    width: ${(props) => (props.size !== 'big') ? '32px' : '48px'};
    height: ${(props) => (props.size !== 'big') ? '32px' : '48px'};
  }
`;

export const Provider = (props) => {
  const { item } = props;
  const history = useHistory();

  const gotoStore = () => {
    document.documentElement.scrollTop = document.body.scrollTop = 0;
    history.push({
      pathname: LOCAL_URL.STORE,
      state: { 
        source: 'provider',
        context: item,
      } 
    });
  }
  return(
    <ProviderContainer {...props} >
      { !item.tag ? null :
        <ProviderTag {...props}>
        { `${item.tag}`}
        </ProviderTag>
      }
      <ProviderCover>
        <img src={ item.img || "https://images.unsplash.com/photo-1548943487-a2e4e43b4853?ixid=MXwxMjA3fDB8MHxzZWFyY2h8Mjh8fHNvdXB8ZW58MHx8MHw%3D&ixlib=rb-1.2.1&auto=format&fit=crop&w=400&q=60"} alt="cover" />
      </ProviderCover>
      <ProviderFooter onClick={gotoStore}>
        <ProviderBrief {...props} >
          <h4>{item.name}</h4>
          <VendorRatingLocation {...item} />
        </ProviderBrief>
        <ProviderIcon {...props} ><img src={item.icon} alt="pop icon" /></ProviderIcon>
      </ProviderFooter>
    </ProviderContainer>
  );
}
