import styled from 'styled-components';
import { THEME } from '../config';

const MAX_WIDTH  = THEME.size.maxWidth.body;
const SIDE_SPACE = THEME.size.sideSpace;

/**
  宽度 100%
  具体页面再决定：
  1. 上下的 margin
  2. 背景色
  3. 字体大小（在局部动态自适应调整相关布局）
 */
export const Section = styled.section`
  width: 100%;
  background: #fff;
`;

/**
  在 Section 之内使用
  屏幕宽度小于最大宽度设定时：两边按规定留白
  屏幕宽度大于最大宽度设定时：内容居中，剩余宽度两边留白
  具体页面使用时，决定 padding
 */
export const StretchColumn = styled.div`
  margin: 0 ${SIDE_SPACE};
  @media screen and (min-width: ${MAX_WIDTH}) {
    max-width: ${MAX_WIDTH};
    margin: 0 auto;
  }
`;



/**
  如果这里不设置 height: 100vh
  那么，DrawerBox 的透明背景和 Material Drawer 的蒙版，还是有痕迹显露出来
  因为设置了 height 100vh
  所以，需要设置 ReceiptBox position 为 relative
  然后，设置 DrawerContainer position 为 absolute bottom 0
 */
export const DrawerBox = styled.div`
  width: 100%;
  height: 100vh;
  padding-top: 4em;
  border: transparent;
  position: relative;
`;
export const DrawerCloseButtonArea = styled.div`
  position: absolute;
  top: 0;
  right: .8em;
  transform: translateY(-120%);
  color: #fff;
  cursor: pointer;

  & svg {
    width: 3em;
    fill: #fff;
  }
`;
export const DrawerIconArea = styled.div`
  position: absolute;
  top: 0;
  left: 50%;
  width: 6em;
  height: 6em;
  border-radius: 3em;
  transform: translate(-50%, -50%);
  background: #53a35d;
  border: 1em solid #fff;

  display: flex;
  justify-content: center;
  align-items: center;

  & svg {
    width: 2em;
    border-radius: 50%;
  }
`;
export const DrawerContentContainer = styled.div`
  position: absolute;
  bottom: 0;

  padding: 24px;
  width: 100%;
  background: #fff;
  border-radius: 2em 2em 0 0;
  text-align: center;
`;
