import React from 'react';
import { BrowserRouter as Router, Route, Redirect, Link } from 'react-router-dom';

import GlobalStyle from './global.styled';
import { UserContextProvider } from './context';
import ExceptionPage from './pages/Exception';
import UISpecPage from './pages/UISpec';
import WelcomePage from './pages/Welcome';
import StorePage from './pages/Store';
import PaymentAmountPage from './pages/Payment/Amount';
import PaymentBillPage from './pages/Payment/Bill';
import PaymentCardPage from './pages/Payment/Card';
import ReceiptPage from './pages/Payment/Receipt.page';
import QuickPayPage from './pages/Payment/QuickPay';
import RedeemPage from './pages/Redeem';
import CoffeePassPage from './pages/CoffeePass';

import logo from './logo.svg';
import './App.css';
import * as config from './config';

const LOCAL_URL = config.LOCAL_URL;
const Root = () => {
  return (
    <div className="App">
      <header className="App-header">
        <img src={logo} className="App-logo" alt="logo" />
        <p>
          Edit <code>src/App.tsx</code> and save to reload.
        </p>
        <a
          className="App-link"
          href="https://reactjs.org"
          target="_blank"
          rel="noopener noreferrer"
        >
          Learn React
        </a>
        
        <Link className="App-link" to={LOCAL_URL['WELCOME']} >Welcome</Link>
        <Link className="App-link" to={LOCAL_URL['UI_SPEC']} >UI Spec</Link>

      </header>
    </div>
  );
}

const AppRoute = () => {
  return (
    <>
      <Route path={LOCAL_URL['WELCOME']}                component={WelcomePage} />
      <Route path={LOCAL_URL['STORE']}                  component={StorePage} />
      <Route path={LOCAL_URL['PAYMENT_AMOUNT']}         component={PaymentAmountPage} />
      <Route path={LOCAL_URL['PAYMENT_BILL']}           component={PaymentBillPage} />
      <Route path={LOCAL_URL['PAYMENT_CARD']}           component={PaymentCardPage} />
      <Route path={LOCAL_URL['PAYMENT_RECEIPT']}        component={ReceiptPage} />
      <Route path={LOCAL_URL['PAYMENT_QUICK_PAY']}      component={QuickPayPage} />
      <Route path={LOCAL_URL['REDEEM']}                 component={RedeemPage} />
      <Route path={LOCAL_URL['COFFEE_PASS']}            component={CoffeePassPage} />

      <Route path={LOCAL_URL['ROOT']} exact             component={Root} />
      <Route path={LOCAL_URL['HOME']} exact             component={() => <Redirect to={{pathname: LOCAL_URL['WELCOME']}} /> } />
      <Route path={LOCAL_URL['UI_SPEC']}                component={UISpecPage} />
      <Route path={`${LOCAL_URL['EXCEPTION']}`} exact   component={ExceptionPage} />
      <Route path={`${LOCAL_URL['EXCEPTION']}/:type`}   component={ExceptionPage} />
    </>
  );
}

export default function App () { return (
  <UserContextProvider>
  <Router >{ /*ref={ref => thisApp.router = ref}> */}
    {/* <ProviderUser superRequest={superRequest} userCache={userCache} url={userUrl}>
      <ProviderEntities superRequest={superRequest}> */}
        <GlobalStyle />
        <Route component={AppRoute}/>
      {/* </ProviderEntities>
    </ProviderUser> */}
  </Router>
  </UserContextProvider>
)}
