import styled from 'styled-components';

const Bars = styled.div`
  width: 2em;
  height: .2em;
  background: ${(props) => (!!props.open ? 'transparent' : (props.color || '#999'))} ;
  
  display: inline-block;
  position: relative;

  transition: all .2s ease;

  &:before,&:after {
    content: '';
    width: 2em;
    height: .2em;
    background: ${(props) => (props.color || '#999')} ;
    display: inline-block;

    position: absolute;
    left: 0;
  }
  &:before {
    top       : ${(props) => (!props.open ? '.5em' : '0')};
    transform : ${(props) => (!props.open ? 'none' : 'rotate(45deg)')};
  }
  &:after {
    top       : ${(props) => (!props.open ? '-.5em' : '0')};
    transform : ${(props) => (!props.open ? 'none' : 'rotate(135deg)')};
  }
`;

const BarsContainer = styled.div`
  display: inline-block;
  width: 2em;
  height: 2em;

  cursor: pointer;
  z-index: 999999;
`;

export const BarsButton = (props) => (
  <BarsContainer>
    <Bars {...props} />
  </BarsContainer>
)
