import React, { useState } from 'react';
import { Link } from 'react-router-dom';
import styled from 'styled-components';
import {
  IconKBArrowL, IconKBArrowR,
  Section, StretchColumn, Provider
} from '../../components';
import { THEME, PROVIDER_ITEM_WIDTH } from '../../config';

const VIEW_MODE_THRETHOLD = THEME.size.viewModeThreshold;

const SegmentSection = styled(Section)`
  padding-top: 1em;

  @media screen and (max-width: ${VIEW_MODE_THRETHOLD}) {
    & ~ & {
      margin-top: .5em;
    }
  }
`;
const SegmentStretch = styled(StretchColumn)`
`;
const HeaderArea =styled.div`
  width: 100%;
  
  display: flex;
  justify-content: space-between;
  align-items: flex-end;
`;
const TitleContainer = styled.div`
  & h1 {
    font-size: 18px;
    line-height: 32px;
  }
  & p {
    font-size: 12px;
    line-height: 12px;
  }
  @media screen and (min-width: ${VIEW_MODE_THRETHOLD}) {
    & h1 {
      font-size: 32px;
      line-height: 32px;
    }
    & p {
      font-size: 16px;
      line-height: 32px;
    }
  }
`;
const ActionContainer = styled.div`
  display: flex;
  align-items: center;
`;
const ActionButton = styled.button`
  cursor: pointer;
  margin-left: .3em;
  width: 1.6em;
  height: 1.6em;
  border-radius: 1.6em;
  border: none;
  font-size: 1.3em;
  display: none;
  &.left {
    margin-left: 1em;
  }
  @media screen and (min-width: ${VIEW_MODE_THRETHOLD}) {
    display: inline-block;
  }
`;
const ViewWindow = styled.div`
  width: 100%;
  overflow-x: scroll;
  white-space: nowrap;

  /* scrollbar-width: none;
  &:-webkit-scrollbar {
    display: none;
  } */

  @media screen and (min-width: ${VIEW_MODE_THRETHOLD}) {
    overflow: hidden;
  }
`;
const ContentList = styled.div`
  width: ${(props) => props.width};
  display: inline-flex;
  justify-content: space-between;
  @media screen and (min-width: ${VIEW_MODE_THRETHOLD}) {
    width: 100%;
  }
`;
export const OfferingProviders = ({title, subtitle, items}) => { 
  return (
    <SegmentSection>
      <SegmentStretch>

        <HeaderArea>
          <TitleContainer>
            <h1>{title}</h1>
            <p>{subtitle}</p>
          </TitleContainer>
          <ActionContainer>
            <Link style={{color: THEME.color.secondary.bg, fontWeight: 500, textDecoration: 'none', whiteSpace: 'nowrap'}} to="#">View all</Link>
            <ActionButton className="left"><IconKBArrowL /></ActionButton>
            <ActionButton><IconKBArrowR /></ActionButton>
          </ActionContainer>
        </HeaderArea>
        
        <ViewWindow>
          <ContentList width={`${(PROVIDER_ITEM_WIDTH.MOBILE + 5) * items.length}px`}>
          { items.map((item, index) => (
            <Provider key={index} item={item} size='big' />
          )) }
          </ContentList>
        </ViewWindow>

      </SegmentStretch>
    </SegmentSection>
  )
}
