import React from 'react';
// import { useHistory } from 'react-router-dom';
import { useForm } from 'react-hook-form';
import { yupResolver } from '@hookform/resolvers/yup';
import * as yup from 'yup';
import { SubmitButton, Input } from '../../components';
import { Form } from './Form';

const FORM_TITLE = `<h1>You’re going to</h1><h1><span>love</span> this...</h1>`;
const FORM_COMMENTS = '<p>Simply enter your email below to join thousands of others enjoying the Lovin Local community</p>';

const schema = yup.object().shape({
  email: yup
        .string()
        .matches(/^[A-Za-z0-9]+([_\.][A-Za-z0-9]+)*@([A-Za-z0-9\-]+\.)+[A-Za-z]{2,6}$/, 'Please input a valid email address')
        .required('Email is required'),
});

export default function EmailForm ({onSubmit, initValues}) {
  const { register, handleSubmit, errors,  formState: { isDirty } } = useForm({
    defaultValues: { ...initValues },
    mode: 'onBlur',
    resolver: yupResolver(schema),
  });
  // const history = useHistory();

  return (
    <Form titleHtml={FORM_TITLE} commentsHtml={FORM_COMMENTS} onSubmit={handleSubmit(onSubmit)}>
      <Input
        ref={register}
        error={!!errors.email}
        helperText={errors?.email?.message}
        type='email'
        name='email'
        placeholder='Enter Your Email here'
      />
      <SubmitButton disabled={!isDirty} >Let'me in</SubmitButton>
    </Form>
  )
}
