import React from 'react';
import styled from 'styled-components';

const VARIANT_CLASS = {
  'medium red'  : "shadow02 medium ft-pb14 clr-ft-fff clr-bg-primary",
  'medium white': "shadow02 medium ft-pb14 clr-ft-primary clr-bg-fff",
  'small red'   : "shadow02 small  ft-pb14 clr-ft-fff clr-bg-primary",
  'small white' : "shadow02 small  ft-pb14 clr-ft-primary clr-bg-fff",
  'large red'   : "shadow02 large  ft-pb18 clr-ft-fff clr-bg-primary",
  'large white' : "shadow02 large  ft-pb18 clr-ft-primary clr-bg-fff",
  'large black frame' : "large frame ft-pb18 clr-ft-000",
}

const ButtonContainer = styled.button`
  width: ${(props) => !props.fullWidth ? 'auto' : '100%'};
  border: none;
  border-radius: .5rem;
  padding: 0 1rem;

  &.small {
    height: 2rem;
    line-height: 2rem;
  }
  &.medium {
    height: 2.25rem;
    line-height: 2.25rem;
  }
  &.large {
    height: 3rem;
    line-height: 3rem;
  }
  &.frame {
    background: transparent;
    border: 1px solid #000;
  }
`;

interface ButtonProps {
  children?: any;
  variant?: string;
  [propName: string]: any;
}
export const Button:React.FC<ButtonProps> = ({children, variant, ...props}) => (
  <ButtonContainer {...props}
    className={
      VARIANT_CLASS[ variant || Object.keys(VARIANT_CLASS)[0] ]
       || Object.values(VARIANT_CLASS)[0]
    }
  >
  {children}
  </ButtonContainer>
)

export const SubmitButton = ({ children, ...props }) => {
  return (
    <Button type='submit' variant="large red" fullWidth={true} {...props}>{children}</Button>
  )
}
