import React from 'react';
// import { useHistory } from 'react-router-dom';
import { useForm } from 'react-hook-form';
import { yupResolver } from '@hookform/resolvers/yup';
import NativeSelect, { NativeSelectProps } from '@material-ui/core/NativeSelect';
import TextField, { TextFieldProps } from '@material-ui/core/TextField';
import InputAdornment from "@material-ui/core/InputAdornment";
import * as yup from 'yup';
import { SubmitButton, Input } from '../../components';
import { Form } from './Form';

const FORM_TITLE = `<h1>Get Your Confirmation Code</h1>`;
const FORM_COMMENTS = '<p>Simply enter your phone number and we’ll send you a code to verify your account.</p>';

const schema = yup.object().shape({
  phoneNumber: yup
        .string()
        .matches(/^[1]([3-9])[0-9]{9}$/, 'Please input a valid phone number')
        .required('Phone number is required'),
  areaCode: yup.string().required(''),
});

const AREA_CODE_OPTIONS = ['64', '86'];
const formatAreaCodeLabel = (code) => `+${code}`;
export default function PhoneNumberForm ({onSubmit, initValues}) {
  const { register, handleSubmit, errors,  formState: { isDirty } } = useForm({
    defaultValues: { ...initValues, areaCode: initValues?.areaCode || AREA_CODE_OPTIONS[0] },
    mode: 'onBlur',
    resolver: yupResolver(schema),
  });
  // const history = useHistory();

  return (
    <Form titleHtml={FORM_TITLE} commentsHtml={FORM_COMMENTS} onSubmit={handleSubmit(onSubmit)}>

          <TextField
            type="text"
            variant='outlined' 
            margin="normal" 
            fullWidth
            label={null}
            InputLabelProps={{shrink: false}}
            autoFocus={true}
            error={!!errors.phoneNumber}
            helperText={errors?.phoneNumber?.message}
            name="phoneNumber"
            inputRef={register} 
            InputProps={{ startAdornment: (
              <InputAdornment position="start">
                <NativeSelect
                  disableUnderline
                  style={{ width: '3.8em', borderRight: '1px solid black'}}
                  value={initValues.areaCode}
                  name="areaCode"
                  inputRef={register}
                >
                { AREA_CODE_OPTIONS.map((code, index) => (
                  <option key={index} value={code}>{formatAreaCodeLabel(code)}</option>
                ))}
                </NativeSelect>
              </InputAdornment>
            )}}
          />

      <SubmitButton disabled={!isDirty} >Verify My Account</SubmitButton>
    </Form>
  )
}
