import React from 'react';
import { Link } from 'react-router-dom';
import styled from 'styled-components';
import { Section, StretchColumn, Button } from '../../components';
import { THEME } from '../../config';

const VIEW_MODE_THRETHOLD = THEME.size.viewModeThreshold;

const SegmentSection = styled(Section)`
  padding: 1em 0;
  font-size: 16px;
  @media screen and (max-width: ${VIEW_MODE_THRETHOLD}) {
    margin-top: .5em;
  }
`;
const SegmentStretch = styled(StretchColumn)`
  position: relative;

  & img {
    width: 100%;
    border-radius: 2em;
    object-fit: cover;
    background-position: center;
    background: #FAEBC8;
  }
  
`;
const ContentArea = styled.div`
  position: absolute;
  top: 50%;
  left: 50%;
  transform: translate(-50%, -50%);

  & button {
    display: none;
  }
  @media screen and (min-width: ${VIEW_MODE_THRETHOLD}) {
    display: flex;
    flex-direction: column;
    justify-content: space-around;
    align-items: center;
    & a {
      display: none;
    }
    & button {
      display: block;
      margin-top: 2em;
    }
  }
`;
const BannerText = styled.div`
  & p {
    font-size: 1.1em;
    font-weight: 600;
  }
  & strong {
    font-size: 1.5em;
    font-weight: 900;
  }
  @media screen and (min-width: ${VIEW_MODE_THRETHOLD}) {
    display: inline-flex;
    align-items: center;
    & p, strong {
      font-size: 1.1em;
    }
    & strong {
      margin-left: .4em;
    }
  }
`;

interface BannerProps {
  img: string;
  contentHtml?: string;
  linkText?: string;
  btnText?: string;
  onClick?: () => void;
}
export const Banner: React.FC<BannerProps>  = ({img, contentHtml, linkText, btnText, onClick}) => { 
  return (
    <SegmentSection>
      <SegmentStretch>
        <img src={img} alt="banner" />
        <ContentArea>
          <BannerText dangerouslySetInnerHTML={{__html: contentHtml}} />
          <Link to="#" onClick={onClick}>{linkText}</Link>
          <Button variant="large black frame" onClick={onClick}>{btnText}</Button>
        </ContentArea>
      </SegmentStretch>
    </SegmentSection>
  )
}
