import React from 'react';
import { useHistory } from 'react-router-dom';
import styled from 'styled-components';
import { IconHome, IconSearch, IconReceipt, IconAccount } from './Icons';

const BottomNavbar = styled.div`
  position: fixed;
  bottom: 0;
  width: 100%;
  height: 48px;
  background: #fff;

  display: flex;
  justify-content: space-around;
  align-items: center;
`;
const IconButton = styled.div`
  color: #000;
  & svg {
    width: 24px;
    fill: #323232;
  }
`;
export const Navbar = () => {
  const history = useHistory();
  const onClick = () => {
    document.documentElement.scrollTop = document.body.scrollTop = 0;
    history.push('/')
  }
  return (
    <>
      <BottomNavbar className="shadow02">
        <IconButton onClick={onClick}><IconHome /></IconButton>
        <IconButton><IconSearch /></IconButton>
        <IconButton><IconReceipt /></IconButton>
        <IconButton><IconAccount /></IconButton>
      </BottomNavbar>
      
      {/* 这里设置一个占位，让页面上最后会有一个高度，避免页面上的元素被 Navbar 遮盖 */}
      <div style={{height: '48px'}}/>
    </>
  )
}
