import React, { useState, useEffect, useMemo } from 'react'
import styled from 'styled-components'
// import Switch from '@material-ui/core/Switch'
import { IconArrowForward, Button, Switch } from '../../components'
import { THEME } from '../../config'

import SVGCoffeePassHand  from '../../svg/lil_coffeepass_hand.svg';
import SVGLogo            from '../../svg/lil_logo_lovinlocal.svg';
import SVGCoffeePass      from '../../svg/lil_coffeepass.svg';

const SIZE = 18;
const BG_COLOR = THEME.color.primary.variant;
const HOLE_COLOR = 'transparent';
const CardContainer = styled.div`
  padding: 8px 8px 0;
    background: ${`
      radial-gradient(circle at bottom right, ${HOLE_COLOR} ${SIZE}px, ${BG_COLOR} ${SIZE+1}px) top,
      radial-gradient(circle at top    right, ${HOLE_COLOR} ${SIZE}px, ${BG_COLOR} ${SIZE+1}px) center 40%,
      radial-gradient(circle at bottom right, ${BG_COLOR}   ${SIZE}px, ${BG_COLOR} ${SIZE+1}px) bottom;
    `};
  background-size: 100% 30%, 100% 25%, 100% 45%;
  background-repeat: no-repeat;
  border-radius: 20px;

  line-height: 27px;
  text-align: center;
  color: #fff;
`;
const PassGroup = styled.div`
  width: 100%;
  padding: .75em 1em 0;
  display: flex;
  justify-content: space-around;
`;
const ActionsContainer = styled.div`
  margin-top: 1em;
  line-height: 22px;
  display: flex;
  justify-content: space-between;
  align-items: center;

  & svg {
    fill: ${THEME.color.primary.default};
    width: 1em;
    transform: translateY(.2em);
  }
`;
const DetailsContainer = styled.div`
  margin-top: 1em;
  padding-bottom: 1em;
  line-height: 16px;
  display: flex;
  align-items: flex-start;

  & img {
    margin-right: .5em;
    height: 2em;;
    width: 2em;
    border-radius: 1em;
    background: ${THEME.color.secondary.default};
  }
`;

const IconCoffeePass = ({onClick, valid}) => (
  !!valid ?
  <img onClick={onClick} src={SVGCoffeePassHand} alt='coffee pass' />
  :
  <img onClick={onClick} style={{transform: 'translateY(-15%)'}} src={SVGLogo } alt='coffee pass' />
)

interface PassCardProps {
  onClick?: () => void;
}
export const PassCard:React.FC<PassCardProps> = ({onClick}) => { 
  const [total, setTotal] = useState(5);
  const [available, setAvailable] = useState(total);
  const sub = () => {
    setAvailable((current) => (current > 0) ? (current - 1) : 0 );
  }
  useEffect(() => {
    if (available === 0) {
      setTotal((t) => {
        if (t === 5) {
          setAvailable(3);
          return 3;
        }
        else {
          setAvailable(5);
          return 5;
        }
      })
    }
  }, [available])
  
  const arr = useMemo(() => {
    const result = new Array(total);
    result.fill(1, 0);
    return result;
  }, [total])

  return (
    <CardContainer>
      <p className="ft-rb18" dangerouslySetInnerHTML={{__html: `${available} Large Coffees Available` }} />

      <PassGroup>
      { arr.map((dummy, index) => (
        <IconCoffeePass key={index} valid={index < available} onClick={sub} />
      )) }
      </PassGroup>

    </CardContainer>
  )
}

export const Actions = () => (
  <ActionsContainer>
    <div style={{ lineHeight: '1em', display: 'flex', alignItems: 'center'}} >
      <span className="ft-rb14 clr-ft-gray0-087" >Auto Renew</span>
      <span style={{marginLeft: '.5em'}} />
      <Switch />
    </div>
    
    <p className="ft-rm14 clr-ft-primary" >View History <IconArrowForward /></p>
  </ActionsContainer>
)

export const Details = () => (
  <DetailsContainer>
    <img src={SVGCoffeePass} alt="coffee pass" />
    <div>
      <p className="ft-rr14 clr-ft-gray1-32" >
        To enjoy a large coffee at one of our Coffee Pass partners, simply visit any business you see on Lovin’Local with this Coffee Pass icon.
      </p>
      <p style={{marginTop: '8px',}}  className="ft-rr14 clr-ft-gray1-32" >
        It’s simple and easy to use, so go enjoy a coffee now!
      </p>
    </div>
  </DetailsContainer>
)
