import React, { useContext, useState } from 'react';
import { useHistory } from 'react-router-dom';
import styled from 'styled-components';
import { useForm, useWatch } from 'react-hook-form';
import { yupResolver } from '@hookform/resolvers/yup';
import * as yup from 'yup';

import { UserContext } from '../../context';
import { LOCAL_URL, THEME } from '../../config';
import { BackButton, SubmitButton, Input } from '../../components';
import AMOUNT_FORM_IMAGE from '../../svg/lil_in_store_deal.svg';
import { FormContainer } from './Form';

const AMOUNT_TO_PAY = styled.div`
  margin-top: 20px;
  display: flex;
  justify-content: space-between;
`;

const calculateAmountToPay = (values) => {
  const { amount, excluded } = values;
  return !amount ? 0 : 
        !excluded ? parseInt(amount) :
        (parseInt(amount) - parseInt(excluded));
}

const SCHEMA = {
  amount: yup
          .number()
          .positive()
          .required('Amount is required'),
  excluded: yup
          .number()
          .positive(),
};

const IsolateAmoutToPay = ({ control }) => {
  const values = useWatch<any>({
    control,
    defaultValue: {}
  });

  const amountToPay = calculateAmountToPay(values);
  return <span className='ft-rb24 clr-ft-green'>{`$${amountToPay.toFixed(2)}`}</span>
}

const AmountForm = ({onSubmit, textHtml, showExcluded}) => {
  const schema = yup.object().shape(SCHEMA);
  const { register, control, handleSubmit, errors,  formState: { isDirty } } = useForm({
    // defaultValues: { ...initValues },
    mode: 'onBlur',
    resolver: yupResolver(schema),
  });

  const toSubmit = (values) => {
    console.log('toSubmit')
    onSubmit(values)
  }

  return (
    <FormContainer>
      <img src={AMOUNT_FORM_IMAGE} alt="form image" />
      <p style={{margin: "16px"}} className="ft-pb16 clr-ft-primary" dangerouslySetInnerHTML={{__html:textHtml}} />

      <form noValidate onSubmit={handleSubmit(toSubmit)} >
        <Input
          ref={register}
          error={!!errors.amount}
          helperText={errors?.amount?.message}
          type='amount'
          name='amount'
          placeholder='Enter Total Bill Amount Here'
        />
        { !showExcluded ? null :
          <Input
            ref={register}
            error={!!errors.excluded}
            helperText={errors?.excluded?.message}
            type='excluded'
            name='excluded'
            placeholder='Enter Excluded Amount Here'
          />
        }
        { !showExcluded ? null :
          <AMOUNT_TO_PAY>
            <span className='ft-rb18 clr-ft-gray2-32'>Amount to Pay</span>
            <IsolateAmoutToPay control={control} />
          </AMOUNT_TO_PAY>
        }
        <SubmitButton disabled={!isDirty} style={{margin: '1em 0'}} >Yes, I confirm</SubmitButton>
      </form>
    </FormContainer>
  )
}

export default function PaymentAmountPage ({location}) { 
  const history = useHistory();
  const data = location?.state?.context;

  // console.log(data)
  
  const onConfirm = (values) => {
    document.documentElement.scrollTop = document.body.scrollTop = 0;
    history.push({
      pathname: LOCAL_URL.PAYMENT_BILL,
      state: { 
        source: 'payment amount page',
        context: {
          voucher: data.voucher,
          discount: data.discount,
          amountToPay: calculateAmountToPay(values),
        },
      }
    })
  }
  return (
    <>
      <AmountForm {...data} onSubmit={onConfirm} />
      <BackButton onClick={() => history.goBack()}>{' Back'}</BackButton>
    </>
  )
}
