export default function CoffeePassPage ({location}) { 
  const history = useHistory();
  const gotoPay = () => {
    history.push({
      pathname: LOCAL_URL.PAYMENT_QUICK_PAY
    })
  }
  const activated = location?.state?.context?.activated;

  return (
    <SegmentSection>
      
      {
        !activated ? 
        <>
        <SegmentStretch>
          <Engagement onClick={gotoPay} />
        </SegmentStretch>
        <SegmentStretch style={{padding: '0 8px'}}>
          <CallAction onClick={gotoPay} />
        </SegmentStretch>
        </>
        :
        <>
        <SegmentStretch>
          <PassCard onClick={() => console.log('click')} />
        </SegmentStretch>
        <SegmentStretch  style={{padding: '0 8px'}}>
          <Actions />
        </SegmentStretch>
        <SegmentStretch >
          <Details />
        </SegmentStretch>
        </>
      }
    

      <Divider />

      <SegmentStretch style={{padding: '0 8px'}}>
      { TAGS.map((t) => (
        <Tag key={t} className="ft-rm14 clr-ft-gray2-48 pink">{t}</Tag>
      )) }
      </SegmentStretch>

      <PopularList sideSpace="24px" items={POPULAR_ITEMS} />

      <Navbar />

    </SegmentSection>
  )
}