import React, { useState } from 'react';
import { Link } from 'react-router-dom';
import styled from 'styled-components';
import Radio from '@material-ui/core/Radio';
import RadioGroup from '@material-ui/core/RadioGroup';
import FormControlLabel from '@material-ui/core/FormControlLabel';
import FormControl from '@material-ui/core/FormControl';
import FormLabel from '@material-ui/core/FormLabel';
import {
  IconKBArrowD, IconKBArrowU,
  Section, StretchColumn, Provider
} from '../../components';
import { THEME } from '../../config';
import SVGMapPointer  from '../../svg/map-pointer.svg';

const VIEW_MODE_THRETHOLD = THEME.size.viewModeThreshold;
const SIDE_SPACE = THEME.size.sideSpace;

const SegmentSection = styled(Section)`
  padding: 0 0 2em 0;
`;
const SegmentStretch = styled(StretchColumn)`
  margin: 0 ${(props) => props.sideSpace || SIDE_SPACE};
  display: flex;
  justify-content: space-between;
`;
const FilterArea = styled.div`
  margin: 1em 2.5em 0 0;
  @media screen and (max-width: ${VIEW_MODE_THRETHOLD}) {
    display: none;
  }
  & h1 {
    white-space:nowrap;
    margin-bottom: 1em;
  }
`;

const ContentList = styled.div`
  width: 100%;
  flex-grow: 3;
  display: flex;
  flex-direction: column;
  @media screen and (min-width: ${VIEW_MODE_THRETHOLD}) {
    flex-direction: row;
    justify-content: space-between;
    flex-wrap: wrap;
  }
`;

const AccordionContainer = styled.div`
  & ~ & {
    margin-top: 1em;
  }
`;
const AccordionHeader = styled.div`
  cursor: pointer;
  display: flex;
  justify-content: space-between;
  & h4 {
    white-space:nowrap;
  }
  & h4:nth-child(2) {
    margin-left 2em;
  }
  & svg {
    fill: #323232;
    width: 1em;
  }
`;

const Accordion = ({title, children}) => {
  const [open, setOpen] = useState(false);
  return (
    <AccordionContainer>
      <AccordionHeader onClick={() => setOpen(!open)}>
        <h4>{title}</h4>
        <h4>{!open ? <IconKBArrowD /> : <IconKBArrowU />}</h4>
      </AccordionHeader>
      { !open ? null : children }
    </AccordionContainer>
  )
}
export const PopularList = ({items, ...props}) => { 
  const [sorter, setSorter] = useState(null);

  return (
    <SegmentSection>
      <SegmentStretch {...props} >

        <FilterArea>
          <h1>All cateogries</h1>

          <Accordion title='Sort'>
            <FormControl component="fieldset">
              <RadioGroup aria-label="gender" name="gender1" value={sorter} onChange={(e) => setSorter(e?.target?.value)}>
                <FormControlLabel control={<Radio />} value="favorite" label="Picked for you" />
                <FormControlLabel control={<Radio />} value="popular"  label="Most popular" />
                <FormControlLabel control={<Radio />} value="rating"   label="Rating" />
                <FormControlLabel control={<Radio />} value="near"     label="Near me" />
              </RadioGroup>
            </FormControl>
          </Accordion>
          <Accordion title='Price Range'>
            <p>Price</p>
          </Accordion>

        </FilterArea>

        <ContentList>
        { items.map((item, index) => (<Provider key={index} item={item} />)) }
        </ContentList>
      
      </SegmentStretch>
    </SegmentSection>
  )
}
