import React, { useContext, useState, forwardRef } from 'react';
import { useHistory } from 'react-router-dom';
import styled from 'styled-components';
import { useForm } from 'react-hook-form';
import { yupResolver } from '@hookform/resolvers/yup';
import * as yup from 'yup';
import TextField, { TextFieldProps } from '@material-ui/core/TextField';
import { UserContext } from '../../context';
import { LOCAL_URL, THEME } from '../../config';
import { BackButton, SubmitButton, } from '../../components';

const FormContainer = styled.div`
  font-family: Roboto;
  font-weight: 500;
  font-size: 16px;
`;

const Input = styled.input`
  width: 100%;
  height: 3em;
  padding: .75em .5em .75em 1em;
  border-radius: .25em;
  border: 1px solid ${(props) => !props.error ? '#c7c8ca' : THEME.color.primary.default};
  color: ${THEME.color.gray['#0-087']};

  &:focus {
    border: 1px solid #f00;
  }
  &::placeholder {
    color: ${THEME.color.gray['#5-025']};
  }
`;
const Label = styled.p`
  color: ${THEME.color.gray['#0-087']};

  line-height: 1.5em;
  margin-bottom: .25em;
`;
const ErrMsg = styled.p`
  color: ${THEME.color.primary.default};
  line-height: 1.5em;
  margin-bottom: .25em;
`;
const Hint = styled.p`
  color: ${THEME.color.gray['#3-76']};
  line-height: 1.2em;
  margin: .5em 0 .25em .4em;
  font-family: Roboto;
  font-weight: 700;
  font-size: 14px;
`;
const ExInfo = styled.div`
  margin-top: 1.5em;
  display: flex;
`;

const SCHEMA = {
  cardNumber: yup
          .string()
          .required('Card number is required'),
  holderName: yup
          .string()
          .required('Card holder name is required'),
  expiry: yup
          .string()
          .required('Expired date is required'),
  securityCode: yup
          .string()
          .required('Security code is required'),
};

export const CardNewForm = ({onSubmit}) => {
  const { register, control, handleSubmit, errors,  formState: { isDirty } } = useForm({
    mode: 'onBlur',
    resolver: yupResolver(yup.object().shape(SCHEMA)),
  });

  const toSubmit = (values) => {
    console.log('toSubmit')
    onSubmit(values)
  }

  return (
    <FormContainer>
      <form noValidate onSubmit={handleSubmit(toSubmit)} >
        <Label>Card number</Label>
        <Input type='text' name='cardNumber' placeholder='16 digit card number'
          ref={register}
          error={!!errors.cardNumber}
        />
        { !errors.cardNumber ? null :
          <ErrMsg>{errors?.cardNumber?.message}</ErrMsg>
        }
        <Label style={{marginTop: '1.5em'}}>Cardholder name</Label>
        <Input type='text' name='holderName' placeholder='Name on card'
          ref={register}
          error={!!errors.holderName}
        />
        { !errors.holderName ? null :
          <ErrMsg>{errors?.holderName?.message}</ErrMsg>
        }
        <ExInfo>
          <div style={{marginRight: '.5em'}}>
            <Label>Card expiry</Label>
            <Input type='text' name='expiry' placeholder='MM/YY'
              ref={register}
              error={!!errors.expiry}
            />
            { !errors.expiry ? null :
              <ErrMsg>{errors?.expiry?.message}</ErrMsg>
            }
          </div>
          <div style={{marginLeft: '.5em'}}>
            <Label>Security code</Label>
            <Input type='text' name='securityCode' placeholder='CVV'
              ref={register}
              error={!!errors.securityCode}
            />
            { !errors.securityCode ? null :
              <ErrMsg>{errors?.securityCode?.message}</ErrMsg>
            }
            <Hint>3 or 4 digits on the back of your card.</Hint>
          </div>
        </ExInfo>
        <SubmitButton disabled={!isDirty} style={{margin: '1em 0'}} >Confirm Payment</SubmitButton>
      </form>
    </FormContainer>
  )
}
