import React from 'react';
import { useHistory } from 'react-router-dom';
import styled from 'styled-components';
import { IconDone, IconLogoLovinlocal, Section, Button } from '../../components';
import { THEME, LOCAL_URL } from '../../config';

const Divider = styled.div`
  border-top: 1px solid ${THEME.color.gray['#6-d9']};
`;

const SegmentSection = styled.div`
  width: 100%;
  padding: 8px;
  background: ${THEME.color.gray['#7-f5']};
`;

const ReceiptContainer = styled.p`
  background: #fff;
  border-radius: .5em;
  text-align: center;
  padding: 16px;
`;

const IconBox = styled.div`
  margin: 0 auto;
  width: 64px;
  height: 64px;
  border-radius: 32px;
  background: ${THEME.color.accent.green};
`;

const ListItem = styled.div`
  display: flex;
  flex-wrap: nowrap;
  justify-content: space-between;
  align-items: center;
`;
const ReceiptItem = styled(ListItem)`
  margin-top: 8px;
  line-height: 22px;
`;
const ReceiptAmount = styled(ListItem)`
  margin-top: 4px;
  line-height: 32px;
`;
const FooterArea = styled.div`
  margin-top: 4px;
  display: flex;
  align-items: center;

  & svg {
    margin-right: 8px;
    width: 36px;
    fill: ${THEME.color.primary.default};
  }
`;

export default function ReceiptPage ({ location }) {
  console.log(location);
  const history = useHistory();

  const { context, postActions } = location?.state;
  const { code, merchant } = context || {};

  console.log(postActions)

  const backToStore = (postAction) => {
    history.push({
      pathname: LOCAL_URL.STORE,
      state: { 
        postAction
      }
    })
  }

  return (
    <SegmentSection>
      <ReceiptContainer>
        <IconBox> <IconDone background="none" color="#fff" /> </IconBox>

        <p style={{marginTop: '8px', lineHeight: '32px'}} className="ft-pb24 clr-ft-gray0-087">{'Payment Successful'}</p>
        <p style={{marginTop: '8px', lineHeight: '27px'}} className="ft-rm18 clr-ft-primary">{'Show this Unique Code to the Merchant'}</p>
        { !code ? null : 
          <p style={{marginTop: '8px', lineHeight: '54px'}} className="ft-rb36 clr-ft-gray0-087">{code}</p> 
        }
        <Divider fullWidth style={{marginTop: '8px'}} />
        <p style={{marginTop: '8px', lineHeight: '32px'}} className="ft-rb24 clr-ft-gray2-48">{merchant}</p>
        <p style={{marginTop: '8px', lineHeight: '24px'}} className="ft-rm16 clr-ft-gray2-48">{'15/1/2021 9:45 AM'}</p>

        <ReceiptItem>
          <span className="ft-rm14 clr-ft-gray2-48">Total amont</span>
          <span className="ft-rm18 clr-ft-gray3-76">{`$${'100.00'}`}</span>
        </ReceiptItem>
        <ReceiptItem>
          <span className="ft-rm14 clr-ft-gray2-48">Discount saving</span>
          <span className="ft-rm18 clr-ft-gray3-76">{`$${'30.00'}`}</span>
        </ReceiptItem>
        <ReceiptItem>
          <span className="ft-rm14 clr-ft-gray2-48">Gooddeal voucher</span>
          <span className="ft-rm18 clr-ft-gray3-76">{`$${'5.00'}`}</span>
        </ReceiptItem>

        <Divider fullWidth style={{marginTop: '16px'} }/>
        
        <ReceiptAmount>
          <span className="ft-rb18 clr-ft-gray2-48">Amount paid</span>
          <span className="ft-rb24 clr-ft-green">{`$${'65.00'}`}</span>
        </ReceiptAmount>

        <FooterArea>
          <IconLogoLovinlocal />
          <div style={{textAlign: 'left', lineHeight:'22px'}}>
            <p style={{}} className="ft-rm14 clr-ft-primary">
              Congratulations!
            </p>
            <p style={{}} className="ft-rm14 clr-ft-primary">
              You’ve earned
              <span className="ft-rb18" >{` ${'1000'} `}</span>
              Lovin’Local Points
            </p>
          </div>
        </FooterArea>
      </ReceiptContainer>

      { !postActions ? 
        <Button style={{marginTop: '1em'}} variant="large red" fullWidth={true} onClick={() => backToStore(null)}>Return to Merchant</Button>
        :
        <>
        { postActions.map((actionLabel) => (
          <Button style={{marginTop: '1em'}} variant="large red" fullWidth={true} onClick={() => backToStore(actionLabel)}>{actionLabel}</Button>
        )) }
        </>
      }
    </SegmentSection>
  )
}
