import React from 'react';
// import { useHistory } from 'react-router-dom';
import { useForm } from 'react-hook-form';
import { yupResolver } from '@hookform/resolvers/yup';
import * as yup from 'yup';
import { SubmitButton, Input } from '../../components';
import { Form } from './Form';

const FORM_TITLE = `<h1>Welcome!</h1><h1>What’s your name?</h1>`;
const FORM_COMMENTS = null;

const schema = yup.object().shape({
  firstName: yup
        .string()
        .matches(/^([\u4e00-\u9fa5]{2,20}|[a-zA-Z.\s]{2,20})$/, 'First name should not contain any invalid characters')
        .required('First name is required'),
  lastName: yup
        .string()
        .matches(/^([\u4e00-\u9fa5]{2,20}|[a-zA-Z.\s]{2,20})$/, 'Last name should not contain any invalid characters')
        .required('First name is required'),
});

export default function UserNameForm ({onSubmit, initValues}) {
  const { register, handleSubmit, errors,  formState: { isDirty } } = useForm({
    defaultValues: { ...initValues },
    mode: 'onBlur',
    resolver: yupResolver(schema),
  });
  // const history = useHistory();


  return (
    <Form titleHtml={FORM_TITLE} commentsHtml={FORM_COMMENTS} onSubmit={handleSubmit(onSubmit)}>
      <Input
        ref={register}
        error={!!errors.firstName}
        helperText={errors?.firstName?.message}
        type='text'
        name='firstName'
        placeholder='First Name Here'
      />
      <Input
        ref={register}
        error={!!errors.lastName}
        helperText={errors?.lastName?.message}
        type='text'
        name='lastName'
        placeholder='Last Name Here'
      />
      <SubmitButton disabled={!isDirty} >Create My Account</SubmitButton>
    </Form>
  )
}
