import React from 'react';
import styled from 'styled-components';
import { IconFavorite } from '../Icons';

const FavoriteButtonContainer = styled.div`
  position: absolute;
  top: 0;
  right: 0;
  margin: 1em;
  font-size: 18px;
  font-weight: 500;
  color: ${(props) => props.color || '#4a4a4a'};
  cursor: pointer;

  & svg {
    display: inline-block;
    fill: ${(props) => props.color || '#4a4a4a'};
    width: 1.5rem;
    height: 1.5rem;
  }
`;
interface FavoriteButtonProps {
  onClick: () => void;
  color?: string;
}
export const FavoriteButton:React.FC<FavoriteButtonProps> = ({onClick, color}) => (
  <FavoriteButtonContainer color={color} onClick={onClick }>
    <IconFavorite />
  </FavoriteButtonContainer>
)
