import React from 'react';
import styled from 'styled-components';

export const FormContainer = styled.div`
  width: 280px;
  height: 520px;
  padding: 0 20px;
`;
export const FormHeader = styled.div`
  margin: 32px 0 20px;
  width: 100%;
  text-align: center;
  & h1 {
    font-size: 24px;
    line-height: 24px;
  }
  & span {
    color: red;
  }
`;
export const FormComments = styled.div`
  width: 100%;
  text-align: center;
  & p {
    font-size: 13px;
    color: black;
  }
`;

export const Form = ({ children, titleHtml, commentsHtml, ...props }) => (
  <FormContainer>
    { !titleHtml ? null : <FormHeader dangerouslySetInnerHTML={{__html: titleHtml}} /> }
    { !commentsHtml ? null : <FormComments dangerouslySetInnerHTML={{__html: commentsHtml}} /> }
    <form noValidate {...props} >
      {children}
    </form>
  </FormContainer>
)
