import React, { useContext } from 'react';
import styled from 'styled-components';
import TextField, { TextFieldProps } from '@material-ui/core/TextField';
import InputAdornment from "@material-ui/core/InputAdornment";
import { UserContext } from '../../context';
import {
  IconLogoLabel, IconLocator, IconSearch,
  BarsButton, Button
} from '../../components';
import { THEME } from '../../config';

const VIEW_MODE_THRETHOLD = THEME.size.viewModeThreshold;

const HeaderArea = styled.div`
  height: ${THEME.size.headerHeight};
  padding: 0 1em;
  background: ${THEME.color.primary.default};

  display: flex;
  justify-content: space-between;
  align-items: center;

  @media screen and (max-width: ${VIEW_MODE_THRETHOLD}) {
    &.second {
      display: none;
    }
  }
`;
const BrandBlock = styled.div`
  display: inline-flex;
  align-items: center;

  & svg {
    margin-left: 1em;
    width: 136px;
    height: 24px;
    fill: #fff;
  }
`;
const LocationInfo = styled.p`
  border-radius: 1.5em;
  border: none;
  background: #e5e5e5;
  
  &.first {
    display: none;
  }
  @media screen and (max-width: ${VIEW_MODE_THRETHOLD}) {
    &.first {
      display: inline-block;
    }
    &.second {
      display: none;
    }
  }
`;
const Search = styled.div`
  & svg {
    width: 2em;
  }
`;

export const Header = ({onBarsClick, onLogin}) => { 
  const { model } = useContext(UserContext);
  const isAnonymous = !model?.credential;
  return (
    <>

    <HeaderArea>

      <BrandBlock>
        <BarsButton color="#fff" onClick={onBarsClick} />
        <IconLogoLabel />
      </BrandBlock>

      { (!isAnonymous) ? 
        <LocationInfo className='first'>
          <IconLocator />
          {model?.location || '36 Blacks Road'}
        </LocationInfo>
        :
        <Button variant="small white" onClick={onLogin} >Login</Button>
      }

    </HeaderArea>

    { (isAnonymous) ? null :
      <HeaderArea className='second'>

        <LocationInfo className='second'>
          <IconLocator />
          {model?.location || '36 Blacks Road'}
        </LocationInfo>

        <TextField
          size="small"
          type="text"
          variant='outlined' 
          margin="normal" 
          label={null}
          name="phoneNumber"
          placeholder="What deals are you looking for today?"
          InputLabelProps={{shrink: false}}
          inputProps={{style: { background: 'transparent', width: '17em' }}} 
          InputProps={{ endAdornment: (
            <InputAdornment position="end">
              <Search><IconSearch /></Search>
            </InputAdornment>
          )}}
        />

      </HeaderArea>
    }
    </>
  )
}
